<template>
  <div class="printer-setting">
    <div class="item">
      <em>自动打印小票</em>
      <cube-switch v-model="print" />
    </div>
    <div class="item">
      <em>自动接单</em>
      <cube-switch v-model="order" />
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      print: true,
      order: false
    }
  }
}
</script>

<style lang="stylus" scoped>
.printer-setting {
  padding: 50px 20px;
  .item {
    line-height: 55px;
    display: flex;
    border-bottom: 1px solid #ccc;
    em {
      flex: 1;
      padding-left: 20px;
    }
  }
}
</style>



